.thumb {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
  max-width: 600px;
  width: 100%;

  font-size: 18px;
  font-weight: 400;
  text-align: center;
}
.quote {
  font-style: italic;
}
.author {
  margin-top: 30px;
  font-weight: 600;
}
